/*----------------------------------------*/
/*  01. Video Wrapper
/*----------------------------------------*/

@mixin video-wrapper {
  position: relative;
  width: 100%;

  &::before {
    content: '';
    display: block;
    padding-top: 56.25%;
  }

  & img,
  & iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1;
  }
}

/*----------------------------------------*/
/*  02. Image Overlay & Ratio
/*----------------------------------------*/
@mixin image-overlay ($value) {
  &::after {
    content: '';

    @include abs-full;

    background: rgba(0, 0, 0, $value);
    z-index: 1;
    transition: background-color 0.3s;
  }
}

@mixin ratio ($ratio) {
  position: relative;

  &::before {
    content: '';
    display: block;
    padding-top: calc($ratio * 100%);
  }

  & img {
    @include abs-full;
    z-index: 1;
  }
}

/*----------------------------------------*/
/*  03. Select
/*----------------------------------------*/
@mixin select-in-wrapper {
  appearance: none;
  width: 100%;
  height: 44px;
  margin: 0;
  padding: 0 .625em;
  border: 0;
  background: none;
  line-height: 44px;
  color: var(--bs-dark);

  &:focus {
    outline: var(--bs-white);
  }
}

