  background-repeat: no-repeat;
  background-position-x: 95%;
  background-position-y: 15px;
}

/*----------------------------------------*/
/*  04. Ul Reset
/*----------------------------------------*/
@mixin list-reset {
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style-type: none;
}

/*----------------------------------------*/
/*  05. Icon Size
/*----------------------------------------*/
@mixin icon ($size) {
  width: $size;
  height: $size;

  & svg {
    width: $size;
    height: $size;
  }
}

/*----------------------------------------*/
/*  06. ScrollBar
/*----------------------------------------*/
@mixin scrollbar {
  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
    background-color: transparent;
  }

  &::-webkit-scrollbar {
    width: 12px;
    background-color: var(--primary);
    height: 6px;
  }

  &::-webkit-scrollbar-thumb {
    -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
    background-color: var(--white);
  }
}

@mixin custom-scrollbar {
  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--bs-light-3);
  }

  &::-webkit-scrollbar-track {
    border-left: 1px solid var(--bs-gray-300);
  }
}

/*----------------------------------------*/
/*  07. Animation
/*----------------------------------------*/
@mixin fade-in {
  opacity: 1;
  visibility: visible;
}

@mixin fade-out {
  opacity: 0;
  visibility: hidden;
}

@mixin fade-up {
  opacity: 1;
  transform: translateY(0);
  visibility: visible;
}

@mixin fade-down {
  opacity: 0;
  transform: translateY(50px);
  visibility: hidden;
  transition: 0.3s;
}

/*----------------------------------------*/
/*  08. Input Default
/*----------------------------------------*/
@mixin input-field {
  width: 100%;
  height: 44px;
  padding: 0.5em .625em;
  border: 1px solid var(--border);
  border-radius: 6px;
  color: var(--title);
}

/*----------------------------------------*/
/*  09. Modal
/*----------------------------------------*/
@mixin modal {
  @include fade-out;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: var(--bs-white);
  z-index: 1001;
  pointer-events: none;
}

/*----------------------------------------*/
/*  10. Table Border
/*----------------------------------------*/
@mixin table-border {
  margin-top: 1em;
  border-collapse: collapse;
  border-spacing: 0;
  empty-cells: show;
  border: 1px solid #f0f0f0;

  & tbody th {
    background: var(--light);
  }

  & th,
  & td {
    padding: 0.5em 1em;
    border-left: 1px solid #f0f0f0;
    border-bottom: 1px solid #f0f0f0;
    border-right-width: 0;
    border-top-width: 0;
    font-size: inherit;
    margin: 0;
    overflow: visible;

    & p {
      margin: 0;
    }
  }
}

/*----------------------------------------*/
/*  11. Editor
/*----------------------------------------*/
@mixin wysiwyg {

  & h1,
  & h2,
  & h3,
  & h4,
  & h5,
  & h6,
  & blockquote,
  & ul,
  & figure,
  & .wp-block-image,
  & p {
    margin-top: 1em;
    overflow-wrap: anywhere;
    font-size: 14px;
  }

  & h1,
  & h2,
  & h3,
  & h4,
  & h5,
  & h6 {
    font-weight: bold;
    font-size: 20px;
  }

  & a {
    color: var(--primary);
    text-decoration: underline;
    overflow-wrap: anywhere;

    &:hover {
      color: var(--black);
    }
  }

  & img {
    max-width: 100%;
    height: auto;
  }

  & ol {
    margin-left: 1em;
    padding-left: 0.5em;
    list-style-type: square;

    & ol {
      margin-top: 0.25em;

      &>li {
        font-size: 0.95rem;
      }
    }
  }

  & ul {
    list-style-type: disc;
    margin-left: 1em;
    padding-left: 0.5em;

    & ul {
      margin-top: 0.25em;

      &>li {
        font-size: 0.95rem;
      }
    }
  }

  & li {
    margin-top: 0.25em;
    line-height: 24px;

    &:first-child {
      margin-top: 0;
    }

    &::marker {
      color: var(--primary);
    }
  }

  & .gform_wrapper {
    margin-top: 1em;
  }

  & table {
    @include table-border;
  }

  & blockquote {
    background: linear-gradient(145deg, #f3f3f3, #ffffff);
    padding: 1.5em 2em;
    border-left: 8px solid #FF6F61;
    font-style: italic;
    position: relative;
    font-size: 1.2em;
    color: #333;
    box-shadow: 10px 5px 20px rgba(0, 0, 0, 0.1);
  }

  blockquote::before {
    content: "â€œ";
    font-size: 3em;
    color: #FF6F61;
    position: absolute;
    top: -10px;
    left: 10px;
  }

  & .video-response {
    margin-top: 1em;
    margin-bottom: 1em;
  }

  & code {
    background-color: #f4f4f4;
    color: #c7254e;
    font-family: 'Fira Code', 'Courier New', monospace;
    padding: 0.2em 0.4em;
    border-radius: 4px;
    font-size: 0.95em;
  }

  & pre {
    background-color: #2d2d2d;
    color: #f8f8f2;
    padding: 1em;
    border-radius: 8px;
    overflow-x: auto;
    font-size: 0.95em;
    line-height: 1.5;
    font-family: 'Fira Code', 'Courier New', monospace;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }

  & pre code {
    background: none;
    color: inherit;
    padding: 0;
    font-size: inherit;
  }

  & .wp-block-list.navheading {
    list-style: none;
    padding: 1rem;
    margin: 1rem 0;
    background: #f9f9f9;
    border-left: 4px solid #0073aa;
    border-radius: 8px;
  }

  & .wp-block-list.navheading li {
    margin-bottom: 0.5rem;
  }

  & .wp-block-list.navheading li a {
    text-decoration: none;
    color: #0073aa;
    font-weight: 500;
    display: inline-block;
    transition: all 0.3s ease;
  }

  & .wp-block-list.navheading li a:hover {
    color: #005177;
    padding-left: 5px;
  }
}

/*----------------------------------------*/
/*  12. Bottom
/*----------------------------------------*/
@mixin button-reset {
  background: none;
  border: 0;
}

@mixin button-default {
  display: inline-block;
  height: auto;
  padding: 0.5em .75em;
  font-size: 1rem;
  text-align: center;
  text-decoration: none;
}

@mixin button-small {
  padding: .375em .625em;
}

@mixin button-normal {
  padding: .5em 1em;
}

@mixin button-large {
  padding: .625em 1.25em;
}

@mixin button-style-primary {
  background: var(--primary);
  color: var(--black);
  transition: background 0.3s ease, color 0.3s ease;

  &:hover {
    @media (min-width: 1024px) {
      color: white;
      background: var(--black);
    }
  }
}

@mixin button-style-secondary {
  background: var(--secondary);
  color: var(--white);
  transition: background 0.3s ease, color 0.3s ease;

  &:hover {
    @media (min-width: 1024px) {
      background: var(--primary);
      color: var(--white);
    }
  }
}

@mixin button-style-dark {
  background: var(--black);
  color: var(--white);
  transition: background 0.3s ease, color 0.3s ease;

  &:hover {
    @media (min-width: 1024px) {
      background: var(--primary);
      color: var(--white);
    }
  }
}

@mixin button-style-white {
  background: var(--white);
  color: var(--black);
  transition: background 0.3s ease, color 0.3s ease;

  &:hover {
    @media (min-width: 1024px) {
      background: var(--primary);
      color: var(--white);
    }
  }
}

@mixin button-style-outline-primary {
  border: 1px solid var(--primary);
  background: none;
  transition: background 0.3s ease, color 0.3s ease;

  &:hover {
    @media (min-width: 1024px) {
      border-color: var(--secondary);
    }
  }
}

@mixin button-style-outline-dark {
  border: 1px solid var(--black);
  background: none;
  transition: background 0.3s ease, color 0.3s ease;

  &:hover {
    @media (min-width: 1024px) {
      border-color: var(--primary);
    }
  }
}

@mixin button-link {
  position: relative;
  padding: 0 0 .25em 0;
  background: none;

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 1px;
    transition: all 0.3s;
  }
}

/*----------------------------------------*/
/*  13. CheckBox Style
/*----------------------------------------*/
@mixin custom-checkbox ($color: var(--primary), $border: #f0f0f0) {
  @include abs-full;

  appearance: none;
  opacity: 0;

  &:checked {

    &~span::before,
    &~label::before {
      border-color: $color;
    }

    &~span,
    &~label {
      color: $color;
    }

    &~span::after,
    &~label::after {
      opacity: 1;
    }
  }

  &~span,
  &~label {
    pointer-events: none;
    line-height: 48px;
    transition: color 0.3s cubic-bezier(0.1, 0.1, 0.25, 1);

    @media (min-width: 1024px) {
      line-height: 36px;
    }

    &::before {
      content: '';
      position: absolute;
      left: 0;
      width: 19px;
      height: 19px;
      top: 13px;
      border: 1px solid $border;
      transition: border-color 0.3s cubic-bezier(0.1, 0.1, 0.25, 1);

      @media (min-width: 1024px) {
        top: 7px;
      }
    }

    &::after {
      content: '';
      position: absolute;
      top: 16px;
      left: 3px;
      width: 13px;
      height: 13px;
      background: $color;
      clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
      opacity: 0;

      @media (min-width: 1024px) {
        top: 10px;
      }
    }
  }
}

/*----------------------------------------*/
/*  14. Transition
/*----------------------------------------*/
@mixin transition ($property: all, $duration: 0.3s, $timing: ease) {
  transition: $property $duration $timing;
}

/*----------------------------------------*/
/*  15. Ratio Style
/*----------------------------------------*/
@mixin custom-radio ($color: var(--primary), $border: #f0f0f0) {
  @include abs-full;

  appearance: none;
  opacity: 0;

  &:checked {

    &~span::before,
    &~label::before {
      border-color: $color;
    }

    &~span,
    &~label {
      color: $color;
    }

    &~span::after,
    &~label::after {
      opacity: 1;
    }
  }

  &~span,
  &~label {
    pointer-events: none;

    &::before {
      content: '';
      position: absolute;
      top: 2px;
      left: 0;
      width: 18px;
      height: 18px;
      border: 1px solid $border;
      border-radius: 50%;
      transition: border-color 0.3s cubic-bezier(0.1, 0.1, 0.25, 1);
    }

    &::after {
      content: '';
      position: absolute;
      top: 5px;
      left: 3px;
      width: 13px;
      height: 13px;
      background: $color;
      clip-path: circle(50% at 50% 50%);
      opacity: 0;
    }
  }
}

@mixin radio-wrapper-standard {
  display: inline-block;
  position: relative;
  padding: 0 0.25em;

  & input[type='radio'] {
    display: none;
  }

  & label {
    color: var(--dark-3);
  }

  & label::before {
    content: ' ';
    display: inline-block;
    position: relative;
    top: 5px;
    margin: 0 10px 0 0;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    border: 1px solid var(--border-light);
    background-color: transparent;
  }

  & input[type='radio']:checked+label::after {
    @include radio-wrapper-standard-check;
  }
}

@mixin item-with-line ($border) {
  margin-top: 0.5em;
  padding-top: 0.5em;
  border-top: 1px solid $border;

  &:first-child {
    margin-top: 0;
    padding-top: 0;
    border-top: 0;
  }
}

@mixin line-clamp($lines: 3) {
  display: -webkit-box;
  -webkit-line-clamp: $lines;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}